import React, {useState} from 'react';
import Header from '../Header/Header';
import Footer from '../Footer/Footer';
import Breadcrumbs from '../Breadcrumbs/Breadcrumbs';
import './ServicesPage.scss';
import ServicesCard from '../ServicesCard/ServicesCard';



function ServicesPage() {
    const [isOfferOpen, setIsOfferOpen] = useState(false);
    return (
    <div>
    <Header  isOfferOpen={isOfferOpen} setIsOfferOpen={setIsOfferOpen}/>
        <div id={"top"}></div>
        <section className="servic">
        <div className='servic__left-banner'></div>
            <div className='servic__content'>
                <div className="servic__subheader">
                <Breadcrumbs
                          items={[
                            {label: ' ', url: '#!' },
                            {label: '', url: '#!' },
                             ]}/>
                    <div className="servic__container">
                        <h1 className='servic__title'></h1>
                        <p className='servic__text'>       .</p>
                    </div>
                </div>
            </div>
        <div className='servic__right-banner'></div>
        </section>
        <ServicesCard
            title=""
            text="     ,    ."
            menu1={<>
                    <li> </li>
                    <li> </li>
                    <li> </li></>
                }
            menu2={<>
                    <li>  </li>
                    <li>UX - </li>
                    <li> </li>
                    </>}
            />
        <ServicesCard
            title=""
            text="    ,          ."
            menu1={<>
                    <li>UX/UI </li>
                    <li> </li>
                    <li></li>
                    <li></li>
                    <li></li>
                    </>}
            menu2={<>
                    <li></li>
                    <li> </li>
                    <li></li>
                    <li>NFT</li>
                    </>}
        />
        <ServicesCard
            title="-"
            text="    ,        ."
            menu1={<>
                <li></li>
                <li> </li>
                <li>-</li>
                <li>  </li>
                <li>No-code </li>
                </>}
            menu2={<>
                <li>1 </li>
                <li>Wordpress</li>
                <li>Tilda</li>
                </>}
        />
        <ServicesCard
            title="  "
            text="   24/7."
            menu1={<>
                    <li> </li>
                    <li> </li>
                    <li> </li>
                </>}
        />
        <ServicesCard
            title=""
            text="     -"
            menu1={<>
                    <li>    </li>
                    <li>    </li>
                    <li>   </li>
                    <li>   SEO </li>
            </>}
        />
     <Footer  isOfferOpen={isOfferOpen} setIsOfferOpen={setIsOfferOpen}
    text="  "
    subtext="        ." 
    btn=""
    key="services"
    />
    </div>
    );
  }
  
  export default ServicesPage;
  